/**
 * actions.js
 */
import fetch from 'isomorphic-fetch'
import util from './util'
import _ from 'underscore'

export const ENDPOINT = 'https://192.168.33.10:8089'

// constants for actions
export const REQUEST_JANUS = 'REQUEST_POST'
export const RECEIVE_JANUS = 'RECEIVE_POST'
export const RECEIVE_LONGPOLLING = 'RECEIVE_LONGPOLLING'

// constants for status
export const REQUEST_CREATE_ID = 'REQUEST_CREATE_ID'
export const RESPONSE_CREATE_ID = 'RESPONSE_CREATE_ID'
export const REQUEST_ATTACH = 'REQUEST_ATTACH'
export const RESPONSE_ATTACH = 'RESPONSE_ATTACH'
export const LONGPOLLING_ATTACHED = 'LONGPOLLING_ATTACHED'
export const REQUEST_MEDIATYPE = 'REQUEST_MEDIATYPE'
export const RESPONSE_MEDIATYPE = 'RESPONSE_MEDIATYPE'
export const REQUEST_OFFER = 'REQUEST_OFFER'
export const RESPONSE_OFFER = 'RESPONSE_OFFER'
export const REQUEST_ANSWER = 'REQUEST_ANSWER'
export const RESPONSE_ANSWER = 'RESPONSE_ANSWER'
export const LONGPOLLING_OFFER = 'LONGPOLLING_OFFER'
export const LONGPOLLING_ANSWER = 'LONGPOLLING_ANSWER'
export const LONGPOLLING_DONE = 'LONGPOLLING_DONE'
export const REQUEST_TRICKLE = 'REQUEST_TRICKLE'
export const RESPONSE_TRICKLE = 'RESPONSE_TRICKLE'
export const REQUEST_TRICKLE_COMPLETED = 'REQUEST_TRICKLE_COMPLETED'
export const RESPONSE_TRICKLE_COMPLETED = 'RESPONSE_TRICKLE_COMPLETED'
export const LONGPOLLING_HANGUP = 'LONGPOLLING_HANGUP'
export const LONGPOLLING_KEEPALIVE = 'LONGPOLLING_KEEPALIVE'
export const REQUEST_DETACH = 'REQUEST_DETACH'
export const RESPONSE_DETACH = 'RESPONSE_DETACH'
export const REQUEST_DESTROY = 'REQUEST_DESTROY'
export const RESPONSE_DESTROY = 'RESPONSE_DESTROY'
export const KEEPALIVE = 'KEEPALIVE'
export const UNKNOWN = 'UNKNOWN'

const EXPECTS = {
  REQUEST_CREATE_ID: RESPONSE_CREATE_ID,
  REQUEST_ATTACH:    RESPONSE_ATTACH,
  REQUEST_MEDIATYPE: RESPONSE_MEDIATYPE,
  REQUEST_OFFER: RESPONSE_OFFER,
  REQUEST_ANSWER: RESPONSE_ANSWER,
  REQUEST_TRICKLE: RESPONSE_TRICKLE,
  REQUEST_TRICKLE_COMPLETED: RESPONSE_TRICKLE_COMPLETED,
  REQUEST_DETACH: RESPONSE_DETACH,
  REQUEST_DESTROY: RESPONSE_DESTROY
}

// actions
export function requestJanus(id, janus_type, transaction, jsonBody) {
  return {
    type: janus_type,
    id,
    transaction,
    json: jsonBody
  }
}

export function receiveJanus(id, janus_type, transaction, json) {
  return {
    type: janus_type,
    id,
    transaction,
    json
  }
}

function receiveLongPolling(id, json) {
  if(json.janus === "event" && json.plugindata ) {
    return {
      type: LONGPOLLING_ATTACHED,
      id,
      json
    }
  } else if (json.janus === "keepalive"){
    return {
      type: KEEPALIVE,
      id,
      json
    }
  } else {
    return {
      type: UNKNOWN,
      id,
      json
    }
  }
}

function startLongPolling(id) {
  return (dispatch, getState) => {
    let state = getState()
    let session_id = state.sessions[id] && state.sessions[id].session_id
    let path = _.compact(['janus', session_id]).join("/")

    path = `${path}?rid=${Date.now()}&maxev=1&_=${Date.now()}`

    return fetch(`${ENDPOINT}/${path}`)
      .then( response => response.json() )
      .then( json => {
        dispatch(startLongPolling(id))
        // fixme : longpolling must be stopped when session terminated.
        dispatch(receiveLongPolling(id, json))
      })
  }
}

function fetchJanus(id, janus_type, jsonBody) {
  return (dispatch, getState) => {
    let state = getState()
    let session_id = state.sessions[id] && state.sessions[id].session_id
    let attach_id = state.sessions[id] && state.sessions[id].attach_id
    let path = _.compact(['janus', session_id, attach_id]).join("/")
    let transaction = util.createTransactionId();
    jsonBody = Object.assign({}, jsonBody, {transaction})
    dispatch(requestJanus(id, janus_type, transaction, jsonBody))

    // if(janus_type === REQUEST_ATTACH) dispatch(startLongPolling(id))
    // fixme: longpolling must be stopped, when terminated

    let headers = new Headers()
    headers.append("Content-Type", "application/json")

    let body = JSON.stringify(jsonBody)
    let expect_type = EXPECTS[janus_type]

    let opts = {
      method: 'POST',
      headers,
      body
    }
    return fetch(`${ENDPOINT}/${path}`, opts)
      .then(response => response.json())
      .then(json => {
        dispatch(receiveJanus(id, expect_type, transaction, json))
        if(expect_type === RESPONSE_CREATE_ID) dispatch(startLongPolling(id))
      })
  }
}

export function requestMediatype(id, media_type = {audio: true, video: true}) {
  return dispatch => {
    dispatch(fetchJanus(id, REQUEST_MEDIATYPE, {
      janus: "message",
      body: media_type
    }))
  }
}

export function requestAttach(id, plugin) {
  return (dispatch, getState) => {
    dispatch(fetchJanus(id, REQUEST_ATTACH, {
      janus: "attach",
      plugin: plugin,
    }))
  }
}

export function requestCreateId(id) {
  return (dispatch) => {
    return dispatch(fetchJanus(id, REQUEST_CREATE_ID, {"janus": "create"}))
  }
}



